@import url(http://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Lato", "sans-serif";
    /* overflow-x: hidden; */
    position: relative;
}

.about,
.projects,
.resume,
.contact {
    display: flex;
    height: 90vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
}

@media screen and (max-width: 768px) {
    * {
      overflow: hidden;
    }
  }
.navbar {
  background-color: #353535;
  height: 125px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: #353535;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
  z-index: 9;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: whitesmoke;
  font-size: 23px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 5px;
}

.nav-text a:hover {
  background-color: #ffffff;
  color: #353535;
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #353535;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.item-span {
  margin-left: 16px;
}

.logo-img {
  z-index: 9;
  display: flex;
  justify-content: right;
  align-items: flex-end;
  padding: 15px;
}

@media screen and (max-width: 1250px) {
  .nav-text a {
    width: 95%
  }
}

@media screen and (max-width: 768px) {
  .item-span {
    color: #353535
  }
}

.header {
    margin-left: 50px;
    margin-top: 25px;
}

.project-container {
    display: flex;

    max-width: 800px;
    max-height: 500px;
    margin-left: 15px;
}
.page-container {
    display: flex;
    flex-direction: column;
}

.content-container {
    height: calc(100vh - 140px);
}

@media (max-width: 600px) {
    .content-container {
        height: 100%;
    }
}

